import React from 'react';
import _ from 'underscore';
import MaterialPanel from './MaterialPanel';
import TextField from 'material-ui/lib/text-field';
import Actions from '../actions/AppActions';
import CircularProgress from 'material-ui/lib/circular-progress';
import $ from 'jquery';
import SelectField from 'material-ui/lib/select-field';
import MenuItem from 'material-ui/lib/menus/menu-item';
import RaisedButton from 'material-ui/lib/raised-button';
import FlatButton from 'material-ui/lib/flat-button';
import AuthService from '../utils/AuthService';
import UserStore from '../stores/UserStore';
import Stepper from 'material-ui/lib/Stepper/Stepper';
import Step from 'material-ui/lib/Stepper/VerticalStep';
import FontIcon from 'material-ui/lib/font-icon';

const items = [
    <MenuItem key={1} value={1} primaryText="Light Theme"/>,
    <MenuItem key={2} value={2} primaryText="Dark Theme"/>,
];

class Settings extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: null,
            dockerHost: null,
            dockerPort: null,
            theme: null,
            userId: null,
            username: null,
            activeStep: -1,
            lastActiveStep: 0
        }
    }

    componentWillMount() {
        this.getSettingsForUser();
    }

    selectStep(currentStep) {
        const {
            lastActiveStep,
            activeStep,

            } = this.state;

        if (currentStep > lastActiveStep) {
            return;
        }

        this.setState({
            activeStep: currentStep,
            lastActiveStep: Math.max(lastActiveStep, activeStep),
        });
    }

    continue() {
        const {
            activeStep,
            lastActiveStep,
            } = this.state;

        this.setState({
            activeStep: activeStep + 1,
            lastActiveStep: Math.max(lastActiveStep, activeStep + 1),
        });
    }

    createIcon(step) {
        if (step.props.isCompleted) {
            return (
                <FontIcon className="material-icons" style={{fontSize: 14}}>
                    done
                </FontIcon>
            );
        }

        return <span>{step.props.orderStepLabel}</span>;
    }

    updateCompletedSteps(currentStep) {
        return currentStep < this.state.lastActiveStep;
    }

    saveSettingsForUser() {
        var url = `http://jvmrealtimemetrics.us-west-2.elasticbeanstalk.com:8090/api/settings/save/?userId=${this.state.userId}`;
        $.post({
            url: url,
            data: {
                dockerHost: this.state.dockerHost,
                dockerPort: this.state.dockerPort
            },
            success: (settings) => {
                console.log("settings saved.");
            }
        });
    }

    getSettingsForUser() {
        var profile = UserStore.getState().user;
        var url = `http://jvmrealtimemetrics.us-west-2.elasticbeanstalk.com:8090/api/settings/?userId=${profile.user_id}`;
        $.post({
            url: url,
            success: (settings) => {
                this.setState({
                    dockerHost: settings.dockerHost,
                    dockerPort: settings.dockerPort,
                    userId: profile.user_id,
                    username: profile.nickname
                });
            }
        });

    }

    handleDockerHost = (event) => {
        this.setState({
            dockerHost: event.target.value
        });
    };

    handleDockerPort = (event) => {
        this.setState({
            dockerPort: event.target.value
        });
    };

    render() {
        return (
            <MaterialPanel title={ `Settings for User ${this.state.username}`}>
                <Stepper
                    activeStep={this.state.activeStep}
                    onStepHeaderTouch={this.selectStep}
                    updateCompletedStatus={this.updateCompletedSteps.bind(this, this.props)}
                    createIcon={this.createIcon}
                >
                    <Step orderStepLabel="1" stepLabel="Select Docker Host." actions={[
                      <RaisedButton key={0} label="Continue" primary={true} onClick={this.continue} />,
                      <FlatButton key={1} label="Cancel" />,
                    ]}>
                        <TextField ref="dockerHost" value={this.state.dockerHost}
                            floatingLabelText="Enter your docker host url."
                            onChange={this.handleDockerHost}
                        />
                    </Step>
                    <Step
                        orderStepLabel="2"
                        stepLabel="Select Docker Port."
                        actions={[
                      <RaisedButton key={0} label="Continue" primary={true} onClick={this.continue} />,
                      <FlatButton key={1} label="Cancel" />,
            ]}>
                        <TextField ref="dockerPort" value={this.state.dockerPort}
                                   floatingLabelText="Enter your docker host port."
                                   onChange={this.handleDockerPort}
                        />
                    </Step>

                    <Step orderStepLabel="3" stepLabel="Choose a theme." actions={[
                      <RaisedButton key={0} label="Finish" primary={true} onClick={this.continue} />,
                      <FlatButton key={1} label="Cancel" />,
                    ]}>
                        <SelectField value={this.state.value} floatingLabelText="Choose your theme.">
                            { items }
                        </SelectField>
                    </Step>
                </Stepper>
                <RaisedButton label="Save" secondary={true} onMouseDown={this.saveSettingsForUser}/>
            </MaterialPanel>
        );
    }
}

export default Settings;



